header {
  position: relative;
  height: 100vh;
  padding: 0 20px;
  background-image: url('../../assets/hero-image.jpg');
  background-size: cover;
  background-attachment: fixed;
  background-position: top;
  background-repeat: no-repeat;
  text-align: center;
  color: #fff;
  text-shadow: 0px 2px 1px rgba(0,0,0,.6);
}

nav {
  margin: 20px auto 0;
  padding-top: 20px;
  border-top: 1px solid;
  width: 280px;
}

nav a {
  margin: 0 10px;
  padding: 10px 2px;
  color: #fff;
  text-decoration: none;
  font-weight: bold;
  font-size: 16px;
  border-bottom: 3px solid transparent;
  -webkit-transition: 200ms;
  -o-transition: 200ms;
  transition: 200ms;
  font-family: 'Open Sans';
  text-transform: uppercase;
}

nav a:hover {
  padding: 10px 2px 4px;
  border-color: #fff;
}

.hero-text {
  font-family: 'Baloo';
}

.name,
.tagline {
  margin: 0;
}

.name {
  font-size: 70px;
  padding-top: 100px;
  margin-bottom: -20px;
}

.tagline {
  font-size: 30px;
}



@media (max-width: 600px) {
  nav {
    -ms-flex-pack: center;
        justify-content: center;
  }

  .name {
    font-size: 50px;
    margin-bottom: 10px;
    line-height: 1;
  }

  .tagline {
    font-size: 20px;
  }
}



